import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Form } from '@rocketseat/unform';
import * as Yup from 'yup';

// import { toast } from 'react-toastify';

import { Container, Row } from './styles';

import logo from '../../assets/logo.svg';

import { signInRequest } from '~/store/modules/auth/actions';
import Input from '~/components/Input';

import Alert from '../../util/alert';

const emailSchema = Yup.object().shape({
  email: Yup.string()
    .email('Insira um email válido.')
    .required('Os campos email e senha são obrigatórios.')
});

const passSchema = Yup.object().shape({
  password: Yup.string().required('Os campos email e senha são obrigatórios.')
});

export default function SignIn() {
  const dispath = useDispatch();
  const loading = useSelector(state => state.auth.loading);

  async function handleSubmit({ email, password }) {
    try {
      await emailSchema.validate({ email: email })
    } catch (error) {
      return Alert.warning(error.message)
    }

    try {
      await passSchema.validate({ password: password })
    } catch (error) {
      return Alert.warning(error.message)
    }

    dispath(signInRequest(email, password));
  }

  return (
    <Container>
      <img src={logo} alt="Health eXperience" />
      <Form onSubmit={handleSubmit}>
        <Row>
          <Input name="email" type="text" placeholder="Seu email" />
        </Row>
        <Row>
          <Input name="password" type="password" placeholder="Sua Senha" />
        </Row>
        <button type="submit">
          {loading ? 'Carregando...' : 'Entrar'}
        </button>
      </Form>
    </Container>
  );
}
