import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import colors from '../../styles/colors';
import { FiPower } from 'react-icons/fi';
import logo from '../../assets/logo.svg';

import { Container, Content, Profile, Menu } from './styles';
import { signOut } from '../../store/modules/auth/actions';
import Notifications from '../Notifications';

export default function Header() {
  const profile = useSelector(state => state.user.profile);
  const dispacth = useDispatch();

  function handleSignOut() {
    dispacth(signOut());
  }

  return (
    <Container>
      <Content>
        <nav>
          <Link to="/" className="logo">
            <img src={logo} alt="Health eXperience" />
          </Link>

          <ul>
            <li>
              <Menu activeStyle={{ color: colors.linkActive }} to="/students">
                /alunos
              </Menu>
            </li>
            <li>
              <Menu activeStyle={{ color: colors.linkActive }} to="/enrollments">
                /matrículas
              </Menu>
            </li>
          </ul>
        </nav>

        <aside>
          <Notifications />
          <Profile>
            <div className="actions">
              <span className="logged-user">Olá, {profile.name} </span>
              <span className="logout" onClick={handleSignOut}>
                <FiPower color="#656565" size={18} />
              </span>
            </div>
          </Profile>
        </aside>
      </Content>
    </Container>
  );
}
