import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { FaTrash, FaPlus, FaUserCheck } from 'react-icons/fa';

import colors from '~/styles/colors';
import { Container } from '../../../components/Grid';
import { HeaderPage } from '../../../components/HeaderPage/styles';
import { Controls } from '../../../components/Controls/styles';
import Title from '../../../components/Title';
import { Table, Thead, Th, Tbody, Tr, Td } from '../../../components/Table';
import Loading from '../../../components/Loading';

import {
  enrollmentsSearchRequest,
  enrollmentsDeleteRequest,
} from '../../../store/modules/enrollment/actions';

import ButtonLink from '../../../components/ButtonLink';
import Alert from '../../../util/alert';
import { Panel } from '../../../components/Panel/styles';
import NoResultFound from '../../../components/NoResultFound';
import Pagination from '../../../components/Pagination';
import PaginationInfo from '../../../components/Pagination/PaginationInfo';
import ButtonLikeLink from '../../../components/ButtonLikeLink';
import InputSearch from '../../../components/InputSearch';

import { formatCurrencyBR } from '~/util';

export default function EnrollmentList() {
  const [termSearch, setTermSearch] = useState('');
  const enrollments = useSelector(state => state.enrollment.enrollments);
  const pagination = useSelector(state => state.enrollment.pagination);
  const loading = useSelector(state => state.enrollment.loading);
  const dispatch = useDispatch();

  function loadEnrollments(term = termSearch, page = 1) {
    dispatch(enrollmentsSearchRequest({ term, page }));
  }

  useEffect(() => {
    loadEnrollments(termSearch, 1);
  }, []); // eslint-disable-line

  function handleSearchMain(value, page = 1) {
    setTermSearch(value);
    dispatch(enrollmentsSearchRequest({ term: value, page }));
  }

  function handleDelete(id) {
    Alert.delete().then(result => {
      if (result.value) {
        dispatch(enrollmentsDeleteRequest(id));
      }
    });
  }

  function handleLoadPage(page) {
    loadEnrollments(termSearch, page);
  }

  return (
    <Container>
      <HeaderPage>
        <Title>
          <FaUserCheck size={24} color="#121214" />
          Matrículas
        </Title>
        <Controls>
          <ButtonLink to="/enrollments/new">
            <FaPlus size={16} color="#121214" title="Matricular aluno(a)" />
            <span>Cadastrar</span>
          </ButtonLink>

          <InputSearch
            handleSearch={handleSearchMain}
            placeholder="Buscar por aluno ou plano"
          />
        </Controls>
      </HeaderPage>

      {loading ? (
        <Loading>Carregando...</Loading>
      ) : (
        <Panel>
          {pagination.total === 0 ? (
            <NoResultFound />
          ) : (
            <>
              <Table>
                <Thead>
                  <Tr>
                    <Th>Aluno</Th>
                    <Th align="left">Código de matrícula</Th>
                    <Th align="left">Plano</Th>
                    <Th align="left">Data de cadastro</Th>
                    <Th align="left">Cartão de crédito</Th>
                    <Th align="left">Valor mensal</Th>
                    <Th />
                  </Tr>
                </Thead>
                <Tbody>
                  {enrollments.map(enrollment => (
                    <Tr key={String(enrollment.id)}>
                      <Td  width="20%">{enrollment.student.name}</Td>
                      <Td>{enrollment.enrollment_code}</Td>                      
                      <Td align="left">
                        {enrollment.plan ? enrollment.plan.title : ''}
                      </Td>
                      <Td align="left">{enrollment.startDate}</Td>
                      <Td align="left">****.****.****.{enrollment.credit_card}</Td>
                      <Td align="left">{formatCurrencyBR(enrollment.price)}</Td>
                      <Td align="right">
                        <ButtonLikeLink style={{ color: colors.delete, marginLeft: '10px' }} onClick={() => handleDelete(enrollment.id)}>
                          <FaTrash size={16} color="#ce376e" title="remover matrícula" />
                        </ButtonLikeLink>
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
              <br />
              <PaginationInfo
                page={pagination.page}
                perPage={pagination.perPage}
                totalPage={pagination.totalPage}
                total={pagination.total}
              />
              <br />
              {pagination.totalPage > 1 && (
                <Pagination
                  page={pagination.page}
                  totalPage={pagination.totalPage}
                  align="center"
                  onLoadPage={handleLoadPage}
                />
              )}
            </>
          )}
        </Panel>
      )}
    </Container>
  );
}
